import React, { useEffect, useState, useRef } from "react";



import { getStore, updStore, gen16CodeId, parseDate, fetchPostH, getUrlServerSide, ObjectKeys, fetchEncryptStream, convertArray2Obj, convertArray2ObjGroupby } from "../../../stores/Utils";

import {SearchInput_Cmpt } from "../../../components";

import { OpenModal } from "../../../components/DialogHRM";

import "./dashboard.css"
import { AddEmployee_Cmpt, EmployeeItem_Cmpt } from "..";
import {  getall_employee, search_employee } from "../services/server_fetching";
import { get_balances_by_type } from "../../CuentasContables/services/server_fetching";



const SearchInput = SearchInput_Cmpt();
const AddEmployee = AddEmployee_Cmpt();


const EmployeeItem = EmployeeItem_Cmpt();




const frmNm = "birthcertificate"

const EmployeeDashboard  = ({ data, elmId, updObs }) => {
	const [initialize, setInitialize] = useState(false);
	const [obs, set_obs] = useState(0);

	const [addview, setAddview] = useState(false);
	const [updview, setUpdview] = useState(false);
	const [loading, set_loading] = useState(false);




	useEffect(() => {
		if (!initialize) {
			setInitialize(true);
			set_obs(gen16CodeId())
			get_balances_by_type("global_accounts","global_account_params",handle_getall_employee);
			
			
		}
	});




	const openUpdateView =  () => {
		setAddview(false)
		setUpdview(true)
	}


	



	
	const openAddView =  () => {
		setUpdview(false)
		//setAddview(true)
		let data = {};
		data["zIndex"] = 450;
		data["height"] = "80vh";
		// data['observeResize']=true;
		data["props"] = { minHeight: "1vh", overlay: true };
		data["content"] = 
			<AddEmployee confimr={handle_getall_employee} />
		OpenModal(data);
	}


	

	const handleChange =  (v) => {
		!loading && set_loading(true)
	}


	const handle_search_employee =  (v) => {
		set_loading(true)
		search_employee(cb_result)
	}


	const handle_getall_employee =  (v) => {
		getall_employee(cb_result)
	}

	const cb_result = async (f, v) => {
		set_obs(gen16CodeId())
		set_loading(false);
	};


	

	

	return (

	<div className="">
		<div className="accounts_header">
			<h2>Empleados</h2>
			<div className="_dsplFlx ">
				<p className="group_title addBtn purple" onClick={openAddView}>
					Agregar Empleado
				</p>
				
			</div>
			<div className="_dsplFlx " style={{marginTop:14}}>
				<SearchInput
						placeholder={"Buscar ...."} 
						width={360}
						obs={set_obs}
						fetchData={handle_search_employee}
						loading={loading}
						change={handleChange}
						callempty={handle_getall_employee}
						keyFlds={"searchQry"}
					/>
				<div className="flexSpace"/>
				<div className="_dsplFlx ">
					<div className="total_comprobante_title">
						No de Empleados: 
					</div>
					<div className="total_comprobante gray">
						<span>
							{ObjectKeys(getStore("employees_list"))?.length}
						</span>
					</div>
					
				</div>
			</div>
		</div>

		

		
			<div className="employees_container scollVh">
				{getStore("employees_list") && ObjectKeys(getStore("employees_list"))?.map(cid=>{
					return (
						<EmployeeItem 
							data={getStore("employees_list")[cid]} 
							elmId={cid} 
							key={cid}
							openUpdateView={openUpdateView}
							handleRefreshAll={handle_getall_employee}
						/>
					)
				})}
			</div>
		
		</div>
	);
};

export default EmployeeDashboard;
